}
nav {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  max-width: 1200px;
  margin: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  color: #bdc9c9;
  z-index: 10;
  height: 80px;
  text-transform: uppercase;
}

nav ul {
  list-style: none;
}

nav ul li {
  display: inline-block;
  padding: 10px 0;
  margin: 0 20px;
}

nav ul:last-child li:hover {
  border-bottom: 3px solid #fff;
  margin-bottom: -3px;
  color: #fff;
  cursor: pointer;
}


.first_session {
  position: relative;
  width: 100%;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  overflow: hidden;
}
.first_session .bg_img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top;
}
.first_session .content {
  width: 100%;
  max-width: 1200px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  position: relative;
  height: 100%;
}

.first_session .content img {
  width: 40%;
  max-width: 450px;
  z-index: 5;
}
.first_session .content .mini {
  max-width: 50%;
}
.first_session .content .mini p {
  color: #bdc9c9;
  font-size: 14px;
  line-height: 1.6;
}
.first_session .content .mini h2 {
  font-size: 3em;
  color: #fff;
  text-shadow: 0 2px 0 rgba(0, 0, 0, 0.5);
  position: relative;
}
.first_session .content .mini h2 span {
  -webkit-animation: animate infinite 8s;
          animation: animate infinite 8s;
  text-transform: uppercase;

  color: transparent;
  -webkit-text-stroke: 2px #dfb891;
}

.first_session .content .mini h2 span::before {
  content: "";
  -webkit-animation: animate infinite 8s;
          animation: animate infinite 8s;
}

.first_session .wave {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  object-fit: cover;
}

@media screen and (max-width: 636px) {
    .first_session .content .mini h2 {
        font-size: 2em !important;
    }
    .first_session .content .mini p {
        font-size: 12px;
      }
}

/* Second Session */

.second_session {
  min-height: 100vh;
  color: #000;
  background-color: #fff;
  transition: 0.5s;
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.second_session h2 {
  transition: 0.5s;
  text-align: center;
  padding: 20px;
  text-transform: uppercase;
  font-size: 3em;
}
.second_session h2 span {
  color: transparent;
  -webkit-text-stroke: 2px #dfb891;
}

.second_session .cards_session {
  position: relative;
  width: 95%;
  max-width: 1200px;
  margin: 50px auto;
  height: auto;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-template-rows: auto 1fr auto;
  grid-gap: 20px;
  z-index: 5;
  background-color: #fff;
}
.second_session .cards_session .cards {
  position: relative;
  width: 300px;
  height: 400px;
  margin: 0 auto;
  background: #fff;
  border-radius: 10px;
  transition: 0.5s;
  overflow: hidden;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.2);
}
/* Cards from the left side 1/4/7/10 */
.second_session .cards_session .cards:nth-child(3n + 1) {
  -webkit-transform-origin: bottom left;
          transform-origin: bottom left;
}
/* Cards from the right side 3/6/9/12*/
.second_session .cards_session .cards:nth-child(3n) {
  -webkit-transform-origin: bottom right;
          transform-origin: bottom right;
}
/* Cards from the left side hover */
.second_session .cards_session .cards:nth-child(3n + 1):hover {
  -webkit-transform: rotate(-5deg);
          transform: rotate(-5deg);
}
/* Cards from the middle 2/5/8/11*/
.second_session .cards_session .cards:nth-child(2):hover,
.second_session .cards_session .cards:nth-child(5):hover,
.second_session .cards_session .cards:nth-child(8):hover {
  -webkit-transform: translate(0, -20px);
          transform: translate(0, -20px);
}
/* Cards from the right side hover */
.second_session .cards_session .cards:nth-child(3n):hover {
  -webkit-transform: rotate(5deg);
          transform: rotate(5deg);
}
/* All cards while on hover */
.second_session .cards_session .cards:hover {
  box-shadow: 0px 10px 13px -7px #000000, 0px 0px 14px 13px #dfb891;
  border: 3px solid #dfb891;
}

@media screen and (max-width: 991px) {
    /* Responsive for column of two  */
/* Cards  2/4/6/8/10 */
  .second_session .cards_session .cards:nth-child(2n) {
    -webkit-transform-origin: bottom right;
            transform-origin: bottom right;
  }
  .second_session .cards_session .cards:nth-child(2n):hover {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }
  /* Cards 1/3/5/7/9 */
  .second_session .cards_session .cards:nth-child(2n+1) {
    -webkit-transform-origin: bottom left;
            transform-origin: bottom left;
  }
  .second_session .cards_session .cards:nth-child(2n+1):hover {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }
}
@media screen and (max-width: 675px) {
    .second_session .cards_session .cards {
        -webkit-transform-origin: center !important;
                transform-origin: center !important;
    }
    .second_session .cards_session .cards:hover {
        -webkit-transform: translate(0, -20px) !important;
                transform: translate(0, -20px) !important;
    }
}

.cards .img_box {
  height: 45%;
  width: 100%;
  position: relative;
}
.cards .img_box img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
