}
.cards .info {
  height: 55%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  /* align-items: flex-start; */
  padding: 20px;
}
.cards .info h3,
.cards .info p {
  margin-bottom: 20px;
}

.cards .info a {
  text-decoration: none;
  color: #ae6f2f;
  font-weight: 600;
  transition: 0.5s;
  font-size: small;
  display: -webkit-flex;
