}
.cards .info a .icon {
  margin-left: 2px;
  transition: 0.5s;
}
.cards .info a:hover .icon {
  margin-left: 10px;
}

.second_session .wave {
  z-index: 5;
}


/* Fourth Session */
.fourth_session {
  background-color: #000;
  min-height: 100vh;
  color: #fff;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}
.fourth_session h2 {
  text-align: center;
  font-size: 3em;
  padding: 20px 0;
}
