.SectionContact {
  position: relative;
  /* min-height: 100vh; */
  /* background: #618775; */
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 20px;
  transition: 0.5s ease-in;
}

.SectionContact .container {
  position: relative;
  width: 800px;
  height: 500px;
  background: #fff;
  box-shadow: 0 15px 50px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.SectionContact .container .user {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
}
.SectionContact .imgBx {
  position: relative;
  width: 50%;
  height: 100%;
  transition: 0.5s;
}
.SectionContact .imgBx img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.SectionContact .formBx {
  position: relative;
  width: 50%;
  height: 100%;
  background: #fff;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 40px;
  transition: 0.5s;
}
.SectionContact .formBx h2 {
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-align: center;
  margin-bottom: 10px;
  width: 100%;
  color: #555;
}

.SectionContact .formBx input {
  width: 100%;
  padding: 10px;
  background: #f5f5f5;
  color: #333;
  border: none;
  outline: none;
  box-shadow: none;
  font-size: 14px;
  margin: 8px 0;
  letter-spacing: 1px;
  font-weight: 300;
}

.SectionContact .formBx input[type="submit"] {
  max-width: 100px;
  background: #677eff;
  color: #fff;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 1px;
  transition: 0.5s;
}
.SectionContact .formBx input[type="submit"]:hover {
  cursor: not-allowed;
}

.SectionContact .formBx p {
  position: relative;
  margin-top: 20px;
  font-size: 12px;
  letter-spacing: 1px;
  color: #555;
  text-transform: uppercase;
  font-weight: 300;
}
.SectionContact .formBx a {
  font-weight: 600;
  text-decoration: none;
  color: #577eff;
}
.SectionContact .signinBx .formBx,
.signinBx .imgBx {
  top: 0;
  transition: 0.5s;
}
.SectionContact .container.active .signinBx .formBx {
  top: 100%;
  transition: 0.5s;
}

.SectionContact .container.active .signinBx .imgBx {
  top: -100%;
  transition: 0.5s;
}
.SectionContact .signupBx {
  pointer-events: none;
}
.SectionContact .signupBx input[type="submit"] {
  background-color: #e73e49 !important;
}
.SectionContact .signupBx .formBx {
  top: 100%;
  transition: 0.5s;
}
.SectionContact .signupBx .imgBx {
  top: -100%;
  transition: 0.5s;
}
.SectionContact .container.active .signupBx .formBx,
.SectionContact .container.active .imgBx {
  top: 0;
}
.SectionContact .container.active .signupBx {
  pointer-events: initial;
}
.back_button {
  position: absolute;
  top: 0;
  left: 0;
  margin: 20px 40px;
}
.back_button a {
  text-decoration: none;
  color: #fff;
  font-weight: bolder;
  font-size: 32px;
  transition: 0.5s;
}
.back_button a:hover {
  color: #01bf71;
  border-bottom: 3px solid #01bf71;
  margin-bottom: -3px;
}
@media screen and (max-width: 991px) {
  .SectionContact .container {
    max-width: 400px;
  }

  .SectionContact .imgBx {
    display: none;
  }
  .SectionContact .formBx {
    width: 100%;
  }
  .SectionContact .container.active .signinBx .formBx {
    top: -100%;
  }
}

